.brands-wrapper {
  display: flex;
  flex-direction: column;
  background-color: #06d2cc;
}

.brands-main {
  flex-grow: 1;
  padding-inline: 80px;
}
@media (max-width: 900px) {
  .brands-main {
    padding-inline: 25px;
  }
}

.brands-page .nav-brands {
  padding-inline: 50px;
}

.brands-title {
  padding-block-start: 65px;
  padding-block-end: 20px;
  max-width: 850px;
}
@media (max-width: 500px) {
  .brands-title {
    text-align: center;
  }
}

.brands-title h1 {
  font-size: 100px;
}
@media (max-width: 500px) {
  .brands-title h1 {
    font-size: 40px;
    text-align: center;
  }
}

.brands-text p {
  font-size: 30px;
  font-family: Nunito Sans, sans-serif;
  font-weight: 400;
  max-width: 850px;
}
@media (max-width: 500px) {
  .brands-text p {
    font-size: 22px;
    width: 100%;
    text-align: center;
  }
}

.brands-page .swiper-slide {
  max-width: 300px;
}

.brands-page .header {
  background-color: #f0f0f0;
}

#product-container {
  display: grid;
  justify-items: center;
  grid-template-columns: repeat(auto-fit, minmax(406px, 1fr));
  gap: 30px;
  padding-block-start: 40px;
  padding-block-end: 100px;
}
@media (max-width: 900px) {
  #product-container {
    grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  }
}

.product-logo {
  align-items: center;
  display: flex;
  justify-content: space-between;
  padding-inline: 20px;
  padding-block: 50px;
}
@media (max-width: 900px) {
  .product-logo {
    padding-block: 30px;
  }
}

.product-logo a {
  border: solid 1px black;
  box-shadow: 5px 3px 0 0px rgb(0, 0, 0);
  padding-block: 10px;
  padding-inline: 18px;
  border-radius: 100px;
}

.brands-page .footer_container {
  border: solid 1px black;
}

.brands-page .header {
  position: relative;
  background-color: inherit;
  max-width: 1440px;
}